import React, { Component } from 'react';
import './SideDrawer.css';

class sideDrawer extends Component {
  constructor(props) {
    super(props);
  }
 state={
    isActive:false,
  }


  render() {
    let drawerClasses = 'side-drawer';

    if (this.props.show) {
      drawerClasses = 'side-drawer open';
    }
    return (
      <nav className={drawerClasses}>
        <ul>
          <li>
            <a href="/">The LOGO</a>
          </li>
          <hr />

          <li 
              // className={this.state.isActive ? "" : "active"}          
              onClick={()=>this.setState({isActive: !this.state.isActive})} 
              >
            <a href="">üè° &nbsp; Dashboard</a>
          </li>
          <li 
          className={this.state.isActive ? "" : "active"}          
          onClick={()=>this.setState({isActive: !this.state.isActive})} >
            <a href="/">üöÄ &nbsp; Workspaces</a>
          </li>
          <li 
          // onClick={this.isActiveHandler} className={this.activeClass} 
          >
            <a href="/">üîó &nbsp; Trackable Links</a>
          </li>
          <hr />
          <hr />
          <li>
            <a href="/">‚ù§ &nbsp; Favorites</a>
          </li>
          <li>
            <a href="/">üëÅ &nbsp; Recents</a>
          </li>
          <hr />
          <li className="spacerr">
          </li>
          <li className="spacerh">
            <a href="/">Log In !</a>
          </li>

        </ul>
      </nav>
    );
  }
}
export default sideDrawer;

